header{
	display:flex;
	position: fixed;
	width: 100vw;
	height: 11%;
	justify-content:space-between;
	color: white;
	font-weight: bolder;
	background: rgba(235, 238, 82, 0.445);
	
}

header div{
	display:flex;
	justify-content: space-between;
	align-items: center;
	font-family: Pacifico;
	margin: 0 2rem;
}

header div i{
	font-size: 2rem;
	margin: rem;
}


header nav{
	padding: 1rem 2rem;
}

nav ul li{
	list-style: none;
	display: inline;
	text-transform: uppercase;
	font-weight: bold;
	letter-spacing: 5px;
}

nav li a{
	padding: 1rem;
	margin: 1rem;
	text-decoration: none;
	color: white;
	transition: all 0.5s ease-in;
}

nav li a:hover{
	background: rgba(255,255,255,.3);
	color: black;
}


