/* --App CSS-- */
  body,#root{
    margin:0;
    padding: 0;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.35)), to(rgba(0, 0, 0, 0.35))), url(https://images.pexels.com/photos/7096/people-woman-coffee-meeting.jpg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940) no-repeat;
    background: -webkit-linear-gradient(rgba(0, 0, 0, 0.35), rgba(0, 0, 0, 0.35)), url(https://images.pexels.com/photos/7096/people-woman-coffee-meeting.jpg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940) no-repeat;
    background: linear-gradient(rgba(0, 0, 0, 0.35), rgba(0, 0, 0, 0.35)), url(https://images.pexels.com/photos/7096/people-woman-coffee-meeting.jpg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940) no-repeat;
    background-size: cover;
    color: black;
    font-family: "open sans";
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .container {
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-areas: "Header Header Header"
    "Main Main Main"
    "Footer Footer Footer";
    grid-template-columns: 0.5fr 2fr 0.5fr;
    grid-template-rows: 1fr 3fr 1fr;
  }
  .container > div {
    border: 1px dashed #888;
  }
  
  .Header {
    grid-area: Header;
  }
  .Main {
    grid-area: Main;
  }
  .Footer {
    grid-area: Footer;
  }

  
 /* --LEARN/TEACH/SHARE PAGES CSS-- */
