  }
  header:before,
  header:after {
      content: " "; /* 1 */
      display: table; /* 2 */
  }
  
  header:after {
      clear: both;
  }
  
  header {
      *zoom: 1;
  }
  table {
    border-collapse:collapse;
    border-spacing:0;
    width:100%;
    background:#ffffff;
  }
  
  th {
    background:#3c7d37;
    color:#ffffff;
  }
  
  td, th {
    padding:0.5em 1em;
    text-align:left;
    vertical-align:top;
  }
  #transData tr.active td {
    background:#ffff75;
  }
  tbody th {
    background:#2ea879;
  }
  tbody tr:nth-child(2n-1) {
    background:#f1fff1;
    -webkit-transition:all 100ms ease;
            transition:all 100ms ease;
  }
  tbody tr:hover {
      background-color:#ffffbf;
      cursor: pointer;
  }
  .date, .course {
    width:15%;
  }
  .headline {
    width:25%;
