  .Coursename:before,
  .mainHeadline:before,
  .fontawesome-map-marker:before {
    display:none;
  }
  .overlayModal{
    position: fixed;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%)

  }
  @media screen and (max-width: 768px) {
  
    body {font-size:18px;}
    thead {
      display:none;
    }
    tr {
      margin:0.75em 0.25em;
      border-top:3px solid #3c7d37;
      box-shadow:0px 1px 1px 0px #717171;
    }
    td, tr {
      display:block;
      position:relative;
    }
    tr:before {
      content: "";
      width:35%;
      background:#f1fff1;
      height:100%;
      position:absolute;
    }
    tbody tr:nth-child(2n-1) {
      background:transparent;
    }
    tbody tr:hover {
      background-color:#ffffbf;
      cursor:pointer;
    }
    .date, .course, .headline, .user {
      width:100%;
    }
    .date:before, .course:before, .headline:before, .user:before {
      font-weight:bold;
      display:block;
      left:1em;
      position:absolute;
    }
    .date:before {
      content:"Date: ";
    }
    .course:before {
      content:"course: ";
    }
    .headline:before {
      content:"headline: ";
    }
    .user:before {
      content:"user: ";
    }
    .postPublishDate,
    .Coursename,
    .mainHeadline,
    .fontawesome-map-marker {
      display:block;
      padding-left:36%;
      position:relative;
    }
  
  }
  .button {
    font-size: 1em;
    margin-top: 15px;
    float: right;
    padding: 10px;
    color: #fff;
    border: 2px solid #06D85F;
    border-radius: 20px/50px;
    text-decoration: none;
    cursor: pointer;
    -webkit-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
  }
  .button:hover {
    background: #06D85F;
  }
  
  .overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
    -webkit-transition: opacity 500ms;
    transition: opacity 500ms;
    visibility: hidden;
    opacity: 0;
  }
  .overlay:target {
    visibility: visible;
    opacity: 1;
  }
  
  .popup {
    padding: 20px;
    width: 100vw;
    max-width: 550px;
    background: #fff;
    border-radius: 5px;   
