      .log {
        overflow: hidden;
        background-color: rgba(81, 202, 107, 0.596);
        padding: 40px 30px 30px 30px;
        border-radius: 10px;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 400px;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        -webkit-transition: box-shadow 300ms, -webkit-transform 300ms;
        transition: box-shadow 300ms, -webkit-transform 300ms;
        transition: transform 300ms, box-shadow 300ms;
        transition: transform 300ms, box-shadow 300ms, -webkit-transform 300ms;
        box-shadow: 5px 10px 10px rgba(2, 128, 144, 0.2);
      }
  
      .log > input {
        font-family: "Asap", sans-serif;
        display: block;
        border-radius: 5px;
        font-size: 16px;
        background: white;
        width: 100%;
        border: 0;
        padding: 10px 10px;
        /* margin: 15px -10px; */
      }
      .log > button {
        font-family: "Asap", sans-serif;
        cursor: pointer;
        color: #fff;
        font-size: 16px;
        text-transform: uppercase;
        width: 80px;
        border: 0;
        padding: 10px 0;
        margin-top: 10px;
        margin-left: -5px;
        border-radius: 5px;
        margin-right: 10rem;
        background-color: #F03033;
        -webkit-transition: background-color 300ms;
        transition: background-color 300ms;
      }
      .log > button:hover {
        background-color: #FB2326;
      }
      .log::before, .log::after {
        content: '';
        position: absolute;
        width: 600px;
        height: 600px;
        border-top-left-radius: 40%;
        border-top-right-radius: 45%;
        border-bottom-left-radius: 35%;
        border-bottom-right-radius: 40%;
        z-index: -1;
      }
      .log::before {
        left: 40%;
        bottom: -100%;
        background-color: rgba(69, 105, 144, 0.726);
        -webkit-animation: wawes 4s infinite linear;
                animation: wawes 4s infinite linear;
      }
      .log::after {
        left: 35%;
        bottom: -115%;
        background-color: rgba(2, 128, 144, 0.664);
        -webkit-animation: wawes 4s infinite;
                animation: wawes 4s infinite;
      }
      @-webkit-keyframes wawes {
        from {
          -webkit-transform: rotate(0);
                  transform: rotate(0);
        }
        to {
          -webkit-transform: rotate(360deg);
                  transform: rotate(360deg);
        }
      }
      @keyframes wawes {
        from {
          -webkit-transform: rotate(0);
                  transform: rotate(0);
        }
        to {
          -webkit-transform: rotate(360deg);
                  transform: rotate(360deg);
        }
      }


